import { Component, OnInit, OnDestroy } from '@angular/core';
import { FirebaseService } from '../firebase.service';
import { Skills } from '../../interface/about-me/Skills';
import { HttpErrorResponse } from '@angular/common/http';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { ObserableService } from '../obserable.service';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css'],
  animations: [
    trigger('spinner', [
      state('inactive', style({
        transform: 'scale(1)'
      })),
      state('active', style({
        opacity: '0',
        transform: 'scale(1)'
      })),
      transition('inactive => active', animate('500ms ease-in'))
    ]),
    trigger('card', [
      state('inactive', style({
        opacity: '0',
        transform: 'scale(1)'
      })),
      state('active', style({
        opacity: '1',
        transform: 'scale(1)'
      })),
      transition('inactive => active', animate('500ms ease-in'))
    ])
  ]
})
export class SkillsComponent implements OnInit, OnDestroy {
  private skills: Skills = {};
  private loadingSkills: Boolean = true;
  private spinnerState = 'inactive';
  private cardState = 'inactive';


  constructor(private fbService: FirebaseService, private obsService: ObserableService) { }

  ngOnInit() {
    this.fbService.getSkills().subscribe((data: Skills) => {
      this.loadingSkills = false;
      this.skills = data;
      this.loadingOk();
    });
  }
  loadingOk() {
    this.spinnerState = 'active';
    this.cardState = 'active';
  }
  ngOnDestroy() {
    this.obsService.leftMenuOppen.next(false);
  }
}
